@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@400;700&display=swap');

.home {
  font-family: 'IBM Plex Mono', monospace;
  background-color: #DDDDDD;
  margin: 0;
  scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 100vh;
}

.align-right {
  text-align: right;
  margin-top: 1rem; /* optional for spacing */
}

.hero {
  height: 100vh;
  background-color: #dcdcdc;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
  padding: 0 20px;
  scroll-snap-align: start;
}

.hero h1 {
  font-size: 3rem;
  font-weight: 400;
  margin: 0;
  line-height: 1.5;
  color: #111;
}

.hero .pivot {
  display: inline-block;
  transition: transform 0.4s ease-out;
  will-change: transform;
}

.hero .pivot.rotate {
  transform: rotate(10deg);
}

.hero .highlight,
.about-me .highlight {
  color: #ff69b4;
  font-weight: 700;
  text-decoration: none;
}


.about-me,
.case-studies,
.testimonials {
  height: 100vh;
  padding: 40px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  scroll-snap-align: start;
}

.about-me h1,
.case-studies h1,
.testimonials h1 {
  font-size: 2.25rem;
  font-weight: 400;
  margin: 0;
  line-height: 1.5;
  color: #111;
  width: 85%
}

.case-studies {
  background-color: #DDDDDD;
}

.testimonials {
  background-color: #DDDDDD;
}