.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #DDDDDD;
  color: white;
  display: flex;
  gap: 20px;
  padding: 10px 20px;
  align-items: center;
  z-index: 1000;
}

.navbar a {
  color: white;
  text-decoration: none;
}

.navbar a:hover {
  text-decoration: underline;
}