.blog {
    font-family: 'IBM Plex Mono', monospace;
    background-color: #DDDDDD;
    color: #111;
    padding: 80px 20px 40px;
    line-height: 1.6;
    min-height: 100vh;
  }
  
  .blog h1 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .blog-posts {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
  
  .blog-card {
    display: flex;
    flex-direction: row;
    gap: 16px;
    align-items: flex-start;
    background-color: #DDDDDD;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    padding: 16px;
    transition: transform 0.2s;
  }
  
  .blog-card:hover {
    transform: scale(1.02);
  }
  
  .blog-thumbnail {
    width: 120px;
    height: 80px;
    object-fit: cover;
    border-radius: 4px;
  }
  
  .blog-content h3.blog-title {
    font-size: 1.1rem;
    margin: 0;
    font-family: 'IBM Plex Mono', monospace;
  }
  
  .blog-content a {
    text-decoration: none;
    color: #111111;
  }
  
  .blog-content a:hover {
    text-decoration: underline;
  }